            width: 21px;
            position: absolute;
            right: -21px;
            top: 0;
            bottom: 0;
        }
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            @include centering(v);
            right: -5px;
            width: 10px;
            height: 7px;
            background: url(../img/arr-menu.svg) no-repeat center;
            @include tr2;
        }
    }
    li:last-child a {
        border: none;
    }
}

.sub-menu {
    position: absolute;
    top: 0;
    left: 106%;
    min-width: 231px;
    opacity: 0;
    background: #FFFFFF;
    border: 2px solid #EAEAEA;
    box-sizing: border-box;
    border-radius: 12px;
    visibility: hidden;
    padding: 23px 23px 23px 23px;
    @include tr2;
    a {
        display: block;
        padding: 0;
        border: none;
        margin-bottom: 30px;
        font-weight: 500;
        font-size: 12px;
        line-height: 100%;
        color: #5D5D5D;
        &::after {
