.slider-big {
    border-radius: 12px;
    max-width: 1140px;
    width: 100%;
    .slick-next {
        right: 10px;
    }
    .slick-prev {
        left: 10px;
    }
}

.slider-big>.slider-big__item:nth-child(n+2) {
    display: none;
}

.slider-big__item {
    position: relative;
    background: #F9FAFD;
    border-radius: 12px;
    max-width: 1140px;
    width: 100%;
    padding: 123px 0 30px 100px;
    min-height: 424px;
    overflow: hidden;
    z-index: 5;
    .sub-title {
        width: 47%;
        font-weight: 200;
        font-size: 32px;
        line-height: 140%;
        color: #000000;
        margin-bottom: 2px;
    }
    .title {
        width: 47%;
        font-weight: bold;
        font-size: 49px;
        line-height: 100%;
    }
    .sl-decor {
        position: absolute;
        top: 0;
        right: -53px;
        bottom: 0;
        width: 424.5px;
        height: 424px;
        transform: skew( -14deg, 0deg);
        background: #FFE381;
        background-blend-mode: darken;
        z-index: 0;
    }
    img {
        position: absolute;
        max-width: 43%;
        right: 60px;
        top: 51%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
    }
}

.slick-arrow {
    display: block;
    width: 38px;
    height: 38px;
    background-color: #FFFFFF;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
    z-index: 2;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    &::before {
        content: none;
    }
}

.slick-prev {
    background-image: url(../img/prev.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slick-next {
    background-image: url(../img/next.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slick-prev:hover,
.slick-prev:focus {
    outline: none;
    background-color: #FFFFFF;
    background-image: url(../img/prev.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slick-next:hover,
.slick-next:focus {
    outline: none;
    background-color: #FFFFFF;
    background-image: url(../img/next.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slider-small {
